<template>
  <div id="selectRoles">
    <v-select
      multiple
      v-model="roleIds"
      :items="roleList"
      item-text="name"
      item-value="id"
      label="角色"
    ></v-select>
  </div>
</template>

<script>
export default {
  data: () => ({
    roleIds: [],
    roleList: [],
  }),

  props: {
    oldRoleIds: {
      type: Array
    }
  },

  mounted() {
    this.roleIds = this.oldRoleIds;
  },

  computed: {
      
  },

  watch: {
    oldRoleIds(val, newVal) {
      this.roleIds = this.oldRoleIds;
    }
  },

  created() {
    this.initRoles();
  },

  methods: {
    initRoles() {
      this.$http
        .get("/user/api/v1/role/listAll")
        .then(res => {
          if (res.success) {
            this.roleList = res.data;
          }
        })
        .catch(error => {
          this.$store.commit("SET_SNACK", {
            success: false,
            msg: "访问异常：获取角色失败"
          });
        });
    },
  },
};
</script>
